<template>
  <div style="overflow:hidden">
    <div class="containers">
      <img src="../../assets/profile/bg-introduction.png" alt="">
    </div>
    <div class="profile">
      <div class="container">
        <v-title title="公司简介"></v-title>
        <div class="detail">
          <img src="../../assets/profile/img-intro-1.png" alt="">
          <div class="right">
            <div class="text">{{$t('m.introduce')}}</div>
            <div class="group">
              <div class="title">-ThaiPayMall{{$t('m.introduce1')}}</div>
              <div class="text">{{$t('m.introduce2')}}</div>
            </div>
            <div class="group">
              <div class="title">-{{$t('m.payApp')}}</div>
              <div class="text">{{$t('m.introduce3')}}</div>
            </div>
          </div>
        </div>
        <div class="img-group">
          <img src="../../assets/profile/img-intro-2.png" alt="">
          <img src="../../assets/profile/img-intro-3.png" alt="">
          <img src="../../assets/profile/img-intro-4.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang="stylus">
  .containers
    width 1200px
    margin 0 auto
    position relative
    height 438px
    img
      position absolute
      left -360px
  .profile
    margin-top 60px
    .detail
      margin-top 22px
      img
        width 496px
        height 352px
      .right
        display inline-block
        width 698px
        height 352px
        vertical-align top
        .text
          width 648px
          margin-left 44px
          margin-top 24px
          font-family: MicrosoftYaHei;
          font-size: 14px;
          line-height: 1.71;
          text-align: left;
          color: #555555;
        .group
          width 648px
          margin-left 44px
          margin-top 40px
          .title
            font-family: MicrosoftYaHei;
            font-size: 14px;
            line-height: 1.71;
            text-align: left;
            font-weight: bold;
            color: #333;
          .text
            margin-left 0
            margin-top 0
    .img-group
      margin-top 36px
      margin-bottom 76px
      img
        width 396px
        height 242px
</style>


